<template>
  <div class="place">
    <van-nav-bar title="提交订单" left-text="返回" left-arrow  @click-left="back"/>

    <div class="placeBox">
      <div class="address" @click="openPopup">{{userAddress.address}}</div>

      <div class="placeContent">
        <div class="order-item">

          <div class="items" v-for="(item, index) in orderData" :key="index">
            <div class="clearfix">
              <div class="fl pro-img">
                <img class="auto-img" :src="item.goods_small_logo" alt="">
              </div>
              <div class="fl pro-info">
                <div class="fl">
                  <div class="names">
                    <div class="zh-name">{{item.goods_name}}</div>
                    <!-- <div class="en-name">{{item.enname}}</div> -->
                  </div>
                  <div class="rule">{{item.rule}}</div>
                </div>
                <div class="fr">
                  <div class="price">￥{{item.goods_price}}</div>
                  <!-- <div class="count">x{{item.val}}</div> -->
                </div>
              </div>
            </div>
          </div>

          <div class="order-total clearfix">
            <div class="fr clearfix">
              <span class="fl total-count">共计 {{orderData[0].cat_one_id}} 件商品 合计：</span>
              <span class="fl total-price">￥{{total}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-submit-bar :price="total * 100" button-text="立即结算" button-type="info" @submit="commit" />

    <van-popup class="popup-box" v-model="isOpen" position="bottom">
      <van-address-list v-model="aid" :list="addressList" default-tag-text="默认" @select="eleAddress" @add="pushAddress"/>
    </van-popup>
  </div>
</template>


<script>
import "../assets/css/pay.less";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("shopModule"); 
export default {
  data() {
    return {
      isOpen: false,
      aid: '',
      addressList: [],
      userAddress: {
        address: '请选择收货地址',
        phone: '',
        receiver: ''
      },
      orderData: {},
      counts: 0,
      momey:'',
      total: 0,
      sids: []
    };
  },
  computed: {
      ...mapState(["lists","num"])
  },
  created() {
    this.orderData = this.lists;
    this.sum();
    this.addressBar();
    this.placeOrder(this.sids);
  },

  methods: {
    sum() {
        this.total = 0;
        this.orderData.forEach(v => {
        this.total += v.cat_one_id * v.goods_price;
      });
    },
    pushAddress(){
      this.$router.push('/addressbar')
    },
    back(){
      this.$router.go(-1);
    },
    openPopup() {
      this.isOpen = true;
    },

    addressBar() {

      let tokenString = localStorage.getItem('_Tok');

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'GET',
        url: 'http://www.kangliuyong.com:10002/findAddress',
        params: {
          appkey: this.appkey,
          tokenString
        }
      }).then(result => {
        this.$toast.clear();
        if (result.data.code == 20000) {
          result.data.result.forEach(v => {
            let currentAddress = {
              id: v.aid,
              tel: v.tel,
              name: v.name,
              address: v.province + v.city + v.county + v.addressDetail,
              isDefault: Boolean(v.isDefault)
            };

            if (v.isDefault) {
              this.aid = v.aid;

              this.userAddress.address = currentAddress.address;
            }
            
            this.addressList.push(currentAddress);
          })
        }
      }).catch(err => {
        this.$toast.clear();
      })
    },

    eleAddress(item) {
      this.isOpen = false;
      this.address = item.address;

      this.userAddress = {
        address: item.address,
        phone: item.tel,
        receiver: item.name
      }
    },

    placeOrder(sids) {
      let tokenString = localStorage.getItem('_Tok');

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'GET',
        url: 'http://www.kangliuyong.com:10002/commitShopcart',
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids)
        }
      }).then(result => {
        this.$toast.clear();
        if (result.data.code == 50000) {
          result.data.result.forEach(v => {
            this.counts += v.count;
            this.total += v.count * v.price;
          })

          // this.orderData = result.data.result;
        }
      }).catch(err => {
        this.$toast.clear();
      })
    },

    commit() {
      if (this.userAddress.address == '请选择收货地址') {
        this.$toast('请选择收货地址');
        return;
      }

      let tokenString = localStorage.getItem('_Tok');

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'POST',
        url: 'http://www.kangliuyong.com:10002/pay',
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
          phone: this.userAddress.phone,
          address: this.userAddress.address,
          receiver: this.userAddress.receiver
        }
      }).then(result => {
        this.$toast.clear();
        if (result.data.code === 60000) {
          this.$router.push({name: 'Order'});
        }
      }).catch(err => {
        this.$toast.clear();
      })

    }
  }
};
</script>